"use client";

import { useEffect, useRef, useState, useMemo } from "react";
import Map, {
  Marker,
  NavigationControl,
  ViewState,
  Source,
  Layer,
} from "react-map-gl";
import { Location } from "@/lib/mock-data";
import { motion } from "framer-motion";
import {
  MapPin,
  AlertCircle,
  Landmark,
  UtensilsCrossed,
  TreePine,
  ShoppingBag,
  Hotel,
  Calendar,
  Play,
  Pause,
  RotateCcw,
} from "lucide-react";
import { Category } from "@/lib/mock-data";
import { Button } from "@/components/ui/button";

interface MapViewProps {
  locations: Location[];
  selectedLocationId: string | null;
  onLocationSelect: (id: string) => void;
}

// Get Mapbox token from environment variable
// In Next.js, client-side env variables must be prefixed with NEXT_PUBLIC_
const MAPBOX_TOKEN = process.env.NEXT_PUBLIC_MAPBOX_TOKEN || "";

// Função para obter o ícone e cor baseado na categoria
function getCategoryIcon(category: Category) {
  switch (category) {
    case "culture":
      return {
        Icon: Landmark,
        color: "text-blue-600",
        bgColor: "bg-blue-50",
        borderColor: "border-blue-200",
      };
    case "food":
      return {
        Icon: UtensilsCrossed,
        color: "text-orange-600",
        bgColor: "bg-orange-50",
        borderColor: "border-orange-200",
      };
    case "nature":
      return {
        Icon: TreePine,
        color: "text-green-600",
        bgColor: "bg-green-50",
        borderColor: "border-green-200",
      };
    case "shopping":
      return {
        Icon: ShoppingBag,
        color: "text-purple-600",
        bgColor: "bg-purple-50",
        borderColor: "border-purple-200",
      };
    case "stay":
      return {
        Icon: Hotel,
        color: "text-indigo-600",
        bgColor: "bg-indigo-50",
        borderColor: "border-indigo-200",
      };
    case "event":
      return {
        Icon: Calendar,
        color: "text-pink-600",
        bgColor: "bg-pink-50",
        borderColor: "border-pink-200",
      };
    default:
      return {
        Icon: MapPin,
        color: "text-gray-600",
        bgColor: "bg-gray-50",
        borderColor: "border-gray-200",
      };
  }
}

export function MapView({
  locations,
  selectedLocationId,
  onLocationSelect,
}: MapViewProps) {
  const mapRef = useRef<any>(null);
  const [viewState, setViewState] = useState<ViewState>({
    latitude: 34.9671,
    longitude: 135.7727,
    zoom: 11,
    bearing: 0,
    pitch: 0,
    padding: { top: 0, bottom: 0, left: 0, right: 0 },
  });
  const [routeData, setRouteData] = useState<GeoJSON.FeatureCollection | null>(
    null
  );
  const [isPreviewMode, setIsPreviewMode] = useState(false);
  const [animationProgress, setAnimationProgress] = useState(0);
  const [cursorPosition, setCursorPosition] = useState<[number, number] | null>(
    null
  );
  const animationRef = useRef<number | null>(null);
  const routeCoordinatesRef = useRef<[number, number][]>([]);

  // Função auxiliar para parsear hora
  function parseTime(timeStr: string): number {
    const [time, period] = timeStr.split(" ");
    const [hours, minutes] = time.split(":").map(Number);
    let totalMinutes = hours * 60 + minutes;
    if (period === "PM" && hours !== 12) totalMinutes += 12 * 60;
    if (period === "AM" && hours === 12) totalMinutes -= 12 * 60;
    return totalMinutes;
  }

  // Ordenar localizações por dia e hora usando useMemo
  const sortedLocations = useMemo(() => {
    return [...locations].sort((a, b) => {
      if (a.day !== b.day) return a.day - b.day;
      // Converter hora para comparação (ex: "08:00 AM" -> 800)
      const timeA = parseTime(a.time);
      const timeB = parseTime(b.time);
      return timeA - timeB;
    });
  }, [locations]);

  // Buscar rotas do Mapbox Directions API
  useEffect(() => {
    if (sortedLocations.length < 2 || !MAPBOX_TOKEN) return;

    const fetchRoute = async () => {
      try {
        // Criar string de coordenadas para a API
        const coordinates = sortedLocations
          .map((loc) => `${loc.lng},${loc.lat}`)
          .join(";");

        const url = `https://api.mapbox.com/directions/v5/mapbox/walking/${coordinates}?geometries=geojson&access_token=${MAPBOX_TOKEN}`;

        const response = await fetch(url);
        const data = await response.json();

        if (data.code === "Ok" && data.routes && data.routes.length > 0) {
          const route = data.routes[0];
          const geometry = route.geometry as GeoJSON.LineString;

          // Armazenar coordenadas para animação
          routeCoordinatesRef.current = geometry.coordinates as [
            number,
            number
          ][];

          const geojson: GeoJSON.FeatureCollection = {
            type: "FeatureCollection",
            features: [
              {
                type: "Feature",
                properties: {},
                geometry: route.geometry,
              },
            ],
          };
          setRouteData(geojson);
        }
      } catch (error) {
        console.error("Erro ao buscar rota:", error);
        // Fallback: criar linha reta entre pontos
        const lineCoordinates = sortedLocations.map((loc) => [
          loc.lng,
          loc.lat,
        ]) as [number, number][];

        // Armazenar coordenadas para animação
        routeCoordinatesRef.current = lineCoordinates;

        const geojson: GeoJSON.FeatureCollection = {
          type: "FeatureCollection",
          features: [
            {
              type: "Feature",
              properties: {},
              geometry: {
                type: "LineString",
                coordinates: lineCoordinates,
              },
            },
          ],
        };
        setRouteData(geojson);
      }
    };

    fetchRoute();
  }, [sortedLocations, MAPBOX_TOKEN]);

  // Função de easing para animação mais natural
  function easeInOutCubic(t: number): number {
    return t < 0.5 ? 4 * t * t * t : 1 - Math.pow(-2 * t + 2, 3) / 2;
  }

  // Função para interpolar coordenadas ao longo da rota
  function getPointAlongRoute(progress: number): [number, number] | null {
    if (routeCoordinatesRef.current.length === 0) return null;

    // Aplicar easing para movimento mais natural
    const easedProgress = easeInOutCubic(progress);

    const totalLength = routeCoordinatesRef.current.length - 1;
    const exactIndex = easedProgress * totalLength;
    const index = Math.floor(exactIndex);
    const fraction = exactIndex - index;

    if (index >= routeCoordinatesRef.current.length - 1) {
      return routeCoordinatesRef.current[
        routeCoordinatesRef.current.length - 1
      ];
    }

    const [lng1, lat1] = routeCoordinatesRef.current[index];
    const [lng2, lat2] = routeCoordinatesRef.current[index + 1];

    // Interpolação linear suave
    return [lng1 + (lng2 - lng1) * fraction, lat1 + (lat2 - lat1) * fraction];
  }

  // Função para criar rota parcial baseada no progresso
  function getPartialRoute(progress: number): GeoJSON.FeatureCollection | null {
    if (!routeData || routeCoordinatesRef.current.length === 0) return null;

    const totalLength = routeCoordinatesRef.current.length;
    const endIndex = Math.ceil(progress * (totalLength - 1));

    const partialCoords = routeCoordinatesRef.current.slice(0, endIndex + 1);

    return {
      type: "FeatureCollection",
      features: [
        {
          type: "Feature",
          properties: {},
          geometry: {
            type: "LineString",
            coordinates: partialCoords,
          },
        },
      ],
    };
  }

  // Animação da rota
  useEffect(() => {
    if (!isPreviewMode || routeCoordinatesRef.current.length === 0) {
      if (animationRef.current) {
        cancelAnimationFrame(animationRef.current);
        animationRef.current = null;
      }
      setAnimationProgress(0);
      setCursorPosition(null);
      return;
    }

    // Duração baseada no comprimento da rota (mais dinâmica)
    const baseDuration = 10000; // 10 segundos base
    const routeLength = routeCoordinatesRef.current.length;
    const duration = Math.max(
      5000,
      Math.min(15000, baseDuration * (routeLength / 50))
    );

    const startTime = Date.now();

    const animate = () => {
      const currentTime = Date.now();
      const elapsed = currentTime - startTime;
      const progress = Math.min(elapsed / duration, 1);

      setAnimationProgress(progress);

      const cursorPos = getPointAlongRoute(progress);
      setCursorPosition(cursorPos);

      if (progress < 1) {
        animationRef.current = requestAnimationFrame(animate);
      } else {
        // Quando completa, manter no final por 1.5 segundos e depois resetar
        setTimeout(() => {
          setIsPreviewMode(false);
        }, 1500);
      }
    };

    animationRef.current = requestAnimationFrame(animate);

    return () => {
      if (animationRef.current) {
        cancelAnimationFrame(animationRef.current);
        animationRef.current = null;
      }
    };
  }, [isPreviewMode, routeData]);

  // Fly to location when selected
  useEffect(() => {
    if (selectedLocationId && mapRef.current) {
      const location = locations.find((l) => l.id === selectedLocationId);
      if (location) {
        mapRef.current.flyTo({
          center: [location.lng, location.lat],
          zoom: 14,
          duration: 2000,
          essential: true,
        });
      }
    }
  }, [selectedLocationId, locations]);

  // Show error if token is not configured
  if (!MAPBOX_TOKEN || MAPBOX_TOKEN.trim() === "") {
    return (
      <div className="w-full h-full bg-muted/20 relative overflow-hidden rounded-3xl border border-border/50 shadow-2xl flex items-center justify-center">
        <div className="text-center p-6">
          <AlertCircle className="w-12 h-12 text-destructive mx-auto mb-4" />
          <p className="text-destructive font-medium">
            Mapbox token não configurado
          </p>
          <p className="text-sm text-muted-foreground mt-2">
            Configure VITE_MAPBOX_TOKEN no arquivo .env
          </p>
        </div>
      </div>
    );
  }

  return (
    <div className="w-full h-full bg-muted/20 relative overflow-hidden rounded-3xl border border-border/50 shadow-2xl">
      <Map
        ref={mapRef}
        {...viewState}
        onMove={(evt) => setViewState(evt.viewState)}
        style={{ width: "100%", height: "100%" }}
        mapStyle="mapbox://styles/mapbox/light-v11"
        mapboxAccessToken={MAPBOX_TOKEN}
        attributionControl={false}
      >
        <NavigationControl position="bottom-right" />

        {/* Renderizar rota completa (fundo) */}
        {routeData && !isPreviewMode && (
          <Source id="route" type="geojson" data={routeData}>
            <Layer
              id="route"
              type="line"
              layout={{
                "line-join": "round",
                "line-cap": "round",
              }}
              paint={{
                "line-color": "hsl(199, 89%, 48%)",
                "line-width": 4,
                "line-opacity": 0.7,
              }}
            />
          </Source>
        )}

        {/* Renderizar rota parcial animada */}
        {isPreviewMode &&
          (() => {
            const partialRoute = getPartialRoute(animationProgress);
            return partialRoute ? (
              <Source id="route-animated" type="geojson" data={partialRoute}>
                <Layer
                  id="route-animated"
                  type="line"
                  layout={{
                    "line-join": "round",
                    "line-cap": "round",
                  }}
                  paint={{
                    "line-color": "hsl(199, 89%, 48%)",
                    "line-width": 5,
                    "line-opacity": 0.9,
                  }}
                />
              </Source>
            ) : null;
          })()}

        {/* Cursor animado */}
        {isPreviewMode && cursorPosition && (
          <Marker
            latitude={cursorPosition[1]}
            longitude={cursorPosition[0]}
            anchor="center"
          >
            <motion.div
              initial={{ scale: 0, opacity: 0 }}
              animate={{
                scale: [1, 1.2, 1],
                opacity: 1,
              }}
              transition={{
                scale: {
                  duration: 1.5,
                  repeat: Infinity,
                  ease: "easeInOut",
                },
                opacity: {
                  duration: 0.3,
                },
              }}
              className="relative"
            >
              {/* Cursor principal */}
              <div className="w-5 h-5 bg-primary rounded-full border-2 border-white shadow-xl z-10 relative" />
              {/* Efeito de pulso */}
              <motion.div
                className="absolute inset-0 w-5 h-5 bg-primary/40 rounded-full"
                animate={{
                  scale: [1, 2, 1],
                  opacity: [0.6, 0, 0.6],
                }}
                transition={{
                  duration: 2,
                  repeat: Infinity,
                  ease: "easeOut",
                }}
              />
              {/* Sombra */}
              <div className="absolute -bottom-1 left-1/2 -translate-x-1/2 w-3 h-1 bg-black/20 rounded-full blur-sm" />
            </motion.div>
          </Marker>
        )}

        {sortedLocations.map((loc) => {
          const { Icon, color, bgColor, borderColor } = getCategoryIcon(
            loc.category
          );
          const isSelected = selectedLocationId === loc.id;

          return (
            <Marker
              key={loc.id}
              latitude={loc.lat}
              longitude={loc.lng}
              anchor="bottom"
              onClick={(e) => {
                e.originalEvent.stopPropagation();
                onLocationSelect(loc.id);
              }}
            >
              <div className="group relative cursor-pointer">
                <motion.div
                  initial={{ scale: 0 }}
                  animate={{
                    scale: 1,
                    rotate: isSelected ? 0 : 0,
                  }}
                  whileHover={{ scale: 1.2 }}
                  className={`
                    p-2 rounded-full shadow-lg transition-all duration-300 border-2
                    ${
                      isSelected
                        ? "bg-primary text-primary-foreground scale-125 z-50 border-primary"
                        : `${bgColor} ${color} ${borderColor} hover:scale-110`
                    }
                  `}
                >
                  <Icon
                    className={`w-5 h-5 ${isSelected ? "fill-current" : ""}`}
                  />
                </motion.div>

                {/* Tooltip on hover */}
                <div className="absolute -top-10 left-1/2 -translate-x-1/2 opacity-0 group-hover:opacity-100 transition-opacity bg-black/80 text-white text-xs py-1 px-2 rounded whitespace-nowrap pointer-events-none z-10">
                  {loc.name}
                </div>
              </div>
            </Marker>
          );
        })}
      </Map>

      {/* Decorative gradient overlay for better text readability if we had overlays */}
      <div className="absolute inset-0 pointer-events-none bg-linear-to-b from-black/5 to-transparent h-32" />

      {/* Controles de visualização da rota */}
      {routeData && sortedLocations.length >= 2 && (
        <div className="absolute top-4 left-4 z-10">
          <div className="bg-white/90 backdrop-blur-md shadow-lg border border-border/50 rounded-lg p-2 flex items-center gap-2">
            <Button
              size="icon"
              variant={isPreviewMode ? "default" : "outline"}
              onClick={() => {
                if (isPreviewMode) {
                  setIsPreviewMode(false);
                  setAnimationProgress(0);
                  setCursorPosition(null);
                } else {
                  setIsPreviewMode(true);
                }
              }}
              className="h-8 w-8"
            >
              {isPreviewMode ? (
                <Pause className="w-4 h-4" />
              ) : (
                <Play className="w-4 h-4" />
              )}
            </Button>
            {isPreviewMode && (
              <Button
                size="icon"
                variant="outline"
                onClick={() => {
                  setIsPreviewMode(false);
                  setAnimationProgress(0);
                  setCursorPosition(null);
                }}
                className="h-8 w-8"
              >
                <RotateCcw className="w-4 h-4" />
              </Button>
            )}
            {isPreviewMode && (
              <div className="text-xs text-muted-foreground px-2">
                {Math.round(animationProgress * 100)}%
              </div>
            )}
          </div>
        </div>
      )}
    </div>
  );
}
